import React from 'react'
import"../css/Header.css"
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import VideoCallIcon from '@mui/icons-material/VideoCall';
import AppsIcon from '@mui/icons-material/Apps';
import NotificationsIcon from '@mui/icons-material/Notifications';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import IconButton from '@mui/material/IconButton';
import { useState } from 'react';
import { Link } from 'react-router-dom';



function Header(props) {

const [input,setInput] = useState("");


function checking(){
  props.check()
}


  return (
    <div className='header'>
    <div className='header__left' >
    <IconButton className='burger_icon'><MenuIcon className='MenuIcon' onClick={checking}   /></IconButton>
    
    <Link to='/' Enter >
        <img className="header__logo" src='data:image/png;base64,' alt='' />
      </Link>
    </div>
    <div className='header__input'>
    <input onChange={e =>(setInput(e.target.value))} placeholder='Search' type="text" value={input} />
    <Link to={`/search/${input}`}>
        <SearchIcon className='header__inputButton' />
      </Link>
    </div>
    <div className='header__right' >
      <VideoCallIcon className='header_icon' /> 
      <NotificationsIcon className='header_icon' />
      <AccountCircleIcon className='header_icon' /> 
   
    </div>

    </div>
  )
}

export default Header